// key
export function saveAddKeysToSaleProduct(opt = {}) {
    return (dispatch, getState) => {
        NProgress.start();
        const { key, type, game, cb = () => {} } = opt;
        $.post(`${API_PREFIX}/user/sale/key`, {
            p_id: game.p_id,
            type: type,
            sale_price: game.price,
            s_id: game.s_id,
            key: key
        }).done(res => {
            if (res.Code === 40000) {
                $.get(`${API_PREFIX}/user/sale/${game.s_id}`).done(res2 => {
                    if (res2.Code === 40000) {
                        message.success(res.Msg);
                        cb(res2.data);
                    } else {
                        message.error(res2.Msg);
                    }
                }).fail(err => {
                    message.error(err.statusText);
                }).always(() => {
                    NProgress.done();
                })
            } else {
                message.error(res.Msg);
                NProgress.done();
            }
        }).fail(err => {
            message.error(err.statusText);
            NProgress.done();
        });
    }
}

// key
export function saveRemoveKeysFromSaleProduct(opt = {}) {
    return (dispatch, getState) => {
        NProgress.start();
        const { game, keys, cb = () => {} } = opt;
        // TODO, fixkey
        const numKeys = _.map(keys, _key => {
            return parseInt(_key);
        });
        $.ajax({
            url: `${API_PREFIX}/user/sale/key`,
            type: 'delete',
            data: {
                s_id: game.s_id,
                key_id: JSON.stringify(numKeys)
            },
        }).done(res => {
            if (res.Code === 40000) {
                $.get(`${API_PREFIX}/user/sale/${game.s_id}`).done(res2 => {
                    if (res2.Code === 40000) {
                        message.success(res.Msg);
                        cb(res2.data);
                    } else {
                        message.error(res2.Msg);
                    }
                }).fail(err => {
                    message.error(err.statusText);
                }).always(() => {
                    NProgress.done();
                })
            } else {
                message.error(res.Msg);
                NProgress.done();
            }
        }).fail(err => {
            message.error(err.statusText);
            NProgress.done();
        });
    }
}

