// 
export function isEmail(str = '') {
    const reg = /^.+@.+$/;
    return reg.test(str) || str.trim() === '';
}

// 
export function isPrice(str = '') {
    const reg = /^\d+(\.\d{0,2})?$/;
    return reg.test(str) || str.trim() === '';
}


