// 
export function saleProductList(state = [], action) {
    switch (action.type) {
        case 'LOAD_SALE_PRODUCT_LIST_SUCCESS':
            return action.data;
        default: 
            return state;
    }
}

// 
export function saleProductDetail(state = {}, action) {
    switch (action.type) {
        case 'LOAD_SALE_PRODUCT_DETAIL_SUCCESS':
            return action.data;
        default:
            return state;
    } 
}

